@use "global" as *;

.p-page-contact__text {
    font-size: rem(14);
    letter-spacing: 0.05em;
    font-weight: 300;
    text-align: center;
    @include mq("md") {
        margin-top: rem(40);
        font-size: rem(16);

    }
}

.p-page-contact__content {
    margin-top: rem(30);
    padding: rem(30);
    background-color: #F7F7F7;
    @include mq("md") {
        margin-top: rem(40);
        padding: rem(90);
    }
}
