@use "global" as *;


.wp-pagenavi {
    display: flex;
    justify-content: center;
    gap: rem(10);
}

.wp-pagenavi a, .wp-pagenavi span {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: rem(45);
    height: rem(45);
    border-radius: 50%;
    background-color: #7633A4;
    color: $white;
    font-weight: $bold;
    font-size: rem(20);
    transition: 0.3s ease;
}

.wp-pagenavi a:hover{
    background-color: #F1EBF8;
    color: #4F31A5;
    opacity: 1;
}

.wp-pagenavi span.current{
    background-color: #F1EBF8;
    color: #4F31A5;
}