@use "global" as *;


.p-page-service {

}

.p-page-service__item {
    @include mq("md") {
        display: flex;
        gap: rem(50);
    }
}

.p-page-service__item:not(:first-child) {
    margin-top: rem(50);
    @include mq("md") {
        margin-top: rem(60);
    }
}

.p-page-service__image {
    aspect-ratio: 335/211;
    @include mq("md") {
        width: 40%;
    }
}

.p-page-service__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include mq("md") {
        
    }
}
.p-page-service__text-block {
    margin-top: rem(20);
    @include mq(md) {
        margin-top: 0;
        width: 55.4%;
    }

}

.p-page-service__title {
    text-align: center;
    font-size: rem(18);
    letter-spacing: 0.05em;
    line-height: 1.5;
    font-weight: bold;
    @include mq(md) {
        font-size: rem(24);
        text-align-last: left;
    }
}

.p-page-service__text {
    
    margin-top: rem(15);
    font-size: rem(14);
    letter-spacing: 0.05em;
    line-height: calc(27/15);
    @include mq(md) {
        font-size: rem(15);
        margin-top: rem(20);
    }
}