@use "global" as *;

.p-single-body__inner.l-inner {
    max-width: rem(800);
}

.p-single-body__title h1 {
    font-size: rem(22);
    line-height: calc(46 / 32);
    font-weight: normal;
    @include mq("md") {
        font-size: rem(32);
    } 
}

.p-single-body__meta {
    padding-top: rem(20);
    @include mq("md") {
        padding-top: rem(10);
    } 
}

.p-single-body__image {
    margin-top: rem(14);
    position: relative;
    @include mq("md") {
        margin-top: rem(27);
    } 
}

.p-single-body__image::before {
    content: "";
    display: block;
    padding-top: calc(187 / 335 * 100%);
}

.p-single-body__image img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform:  translate(-50%, -50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-single-body__content {

    p {
        font-size: rem(14);
        line-height: calc(24 / 14);
        font-weight: normal;
        margin-top: rem(30);
        @include mq("md") {
            font-size: rem(15);
            line-height: calc(25 / 15);
            font-weight: normal;
        }
    }
    
    h2{
        font-size: rem(18);
        line-height: 1.5;
        font-weight: $bold;
        margin-top: rem(40);
        padding-left: rem(15);
        position: relative;
        &::before {
            content: "";
            position: absolute;
            width: rem(6);
            height: 100%;
            background-color: #391E88;
            left: rem(-3);
            border-radius: 5px;
        }

        @include mq("md") {
            margin-top: rem(60);
            font-size: rem(24);
            line-height: calc(25 / 15);
            font-weight: normal;
        }
    }

    h3 {
        font-size: rem(16);
        line-height: 1.5;
        margin-top: rem(48);
        @include mq("md") {
            font-size: rem(18);
        }
    }
}