@use "global" as *;

.p-news__inner {
    @include mq("md") {
        display: flex;
        justify-content: center
    }
}
.p-news-content {
    @include mq("md") {
        display: flex;
    } 
}

.p-news__items {
    margin-top: rem(32);
    @include mq("md") {
        margin-top: 0;
        margin-left: rem(50);
    }
}

.p-news__item + .p-news__item {
    margin-top: rem(40);
    @include mq("md") {
        margin-top: rem(15);
    }
}

/*.p-news__item:nth-child(n + 2){
    margin-top: rem(40);
}*/

.p-news-content__meta {
    display: flex;
    align-items: center;
    font-family: $english-font-family;
}

.p-news-content__date {
    color: $black;
    font-size: rem(15);
    letter-spacing: 0.1em;
}

.p-news-content__category {
    font-size: rem(11);
    font-weight: $bold;
    letter-spacing: 0.05em;
    border-radius: 50px;
    line-height: 1;
    padding: rem(4) rem(18);
    background-color: #E8E8E8;
    margin-left: rem(15);
    flex-shrink: 0;
}

.p-news-content__link {
    //margin-top: rem(10);
    @include mq("md") {
        margin-left: rem(20);
        max-width: rem(700);
        // width: 100%;
    }
}

.p-news-content__text {
    font-size: rem(14);
}
