@use "global" as *;

.p-card {

}

.p-card__img {
    position: relative;
}

.p-card__img::before{
    content: "";
    display: block;
    padding-top: 62.686567%;
}

.p-card__img img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-card__body {
    margin-top: rem(14);

}

.p-card__title {
    text-align: center;
    font-size: rem(18);
    font-weight: $bold;
    font-family: $english-font-family;
    letter-spacing: 0.05rem;
}

.p-card__text {
    font-size: rem(14);
    font-weight: 300;
    letter-spacing: 0.05rem;
    line-height: 1.79;
    margin-top: rem(10);
}
