@use "global" as *;

.p-company {
    padding-top: rem(32);
    padding-bottom: rem(96);
    background-image: url(../images/common/company-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: rem(-15) 0;
    @include mq("md") {
        background-image: url(../images/common/company-bg-pc.png);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
        padding-top: rem(136);
    }
}

.p-company--subPage {
    padding-top: rem(32);
    padding-bottom: 0;
    background-image: url(../images/common/page-company-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: rem(-13) rem(-46);
    @include mq("md") {
        background-image: url(../images/common/page-company-bg-pc.png);
        background-repeat: no-repeat;
        background-size: rem(1300);
        background-position: top center;
        padding-top: rem(136);
    }
}

.p-company__inner.l-inner {
    max-width: rem(1300);
}

.p-company__contents {
    margin-top: rem(30);
}

.p-company__subtitle {
    text-align: center;
    font-size: rem(32);
    font-weight: 600;
    letter-spacing: 0.05em;
    line-height: 1.5;

}

.p-company__yellow {
    background: linear-gradient(
        180deg,
        transparent 0%,
        transparent 75%,
        #F5F534 75%,
        #F5F534 75%,
        transparent 95%,
        transparent 100%,
    )
};

.p-company__content {
    margin-top: rem(43);
}

.p-company__text {
    text-align: center;
    font-size: rem(14);
    font-weight: 300;
    letter-spacing: 0.05em;
    line-height: 1.79;
}

.p-company__btn {
    text-align: center;
    margin-top: rem(30);
}

.p-company__message {
    margin-top: rem(60);
    @include mq("md") {
        display: flex;
        justify-content: center;
        gap: rem(50);
        margin-top: rem(160);
    }
}

.p-company__message-image {
    aspect-ratio: 335 / 248;
    @include mq(md) {
        max-width: rem(445);
        width: 100%;
    }
}

.p-company__message-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.p-company__message-text {
    margin-top: rem(30);
    font-size: rem(14);
    line-height: calc(24 / 14);
    letter-spacing: 0.05em;
    @include mq("md") {
        margin-top: 0;
        max-width: rem(605);
        width: 100%;
        font-size: rem(15);
    }

}

.p-company__message-text p:not(:first-child) {
    margin-top: calc(rem(14) * calc(24 / 14));
}
