@use "global" as *;

.sp-nav {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    background-color: #642390;
}

.sp-nav.is-active {
    display: block;
}

.sp-nav__items {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: rem(100) 0;
    min-height: 100vh;
}

.sp-nav__item {
    width: 100%;
}

.sp-nav__item a {
    color: $white;
    text-align: center;
    display: block;
    padding: rem(10) 0;
    color: $white;
    font-family: $english-font-family;
    font-size: rem(15);
    font-weight: $bold;
    line-height: 1;
    letter-spacing: 0.05rem;
}